'use client';

import { useState, useEffect, useRef } from 'react';

// --- Types ---
interface Agent {
  id: string;
  name: string;
  provider: 'Direct' | 'MPC';
  address: string;
  balance: number;
  status: 'IDLE' | 'SCANNING' | 'LOCKED' | 'MOVING';
  color: string;
  avatar: string;
}

interface Message {
  id: number;
  sender: string;
  content: string;
  timestamp: string;
}

import LiveMap from './LiveMap';
import PaymentToast from './PaymentToast';

// --- Constants ---
const INITIAL_AGENTS: Agent[] = [
  { id: 'grok', name: 'Grok (xAI)', provider: 'Direct', address: '0x71C...9A2', balance: 12.50, status: 'IDLE', color: 'bg-blue-500', avatar: 'üöÄ' },
  { id: 'gemini', name: 'Gemini (Google)', provider: 'MPC', address: '0x88B...1C4', balance: 6.20, status: 'IDLE', color: 'bg-purple-500', avatar: '‚ú®' },
  { id: 'gpt', name: 'GPT-4 (OpenAI)', provider: 'MPC', address: '0x2A9...88F', balance: 100.00, status: 'IDLE', color: 'bg-green-500', avatar: 'üß†' },
  { id: 'deepseek', name: 'DeepSeek (V3)', provider: 'Direct', address: '0x99C...3D1', balance: 4.80, status: 'IDLE', color: 'bg-cyan-500', avatar: 'üê≥' },
];

const HOTSPOTS = [
    'UCI Campus (Engineering Hall)',
    'Irvine Spectrum Center',
    'John Wayne Airport (SNA)',
    'Newport Beach Pier',
    'Tustin District Legacy',
    'Diamond Jamboree Plaza',
    'Google Irvine Office'
];

const CYCLE_DURATION = 60; // seconds for faster demo loop

export default function DemoDashboard() {
  const [tick, setTick] = useState(0);
  const [cycleCount, setCycleCount] = useState(1);
  const [agents, setAgents] = useState<Agent[]>(INITIAL_AGENTS);
  const [logs, setLogs] = useState<string[]>([]);
  const [chatMessages, setChatMessages] = useState<Message[]>([]);
  const [globalStatus, setGlobalStatus] = useState('SYSTEM IDLE');
  const [currentRoute, setCurrentRoute] = useState({ from: 'HUB', to: 'HUB' });
  const [isSurge, setIsSurge] = useState(false);
  
  // Toast State
  const [paymentToast, setPaymentToast] = useState<{show: boolean, from: string, to: string, amount: number}>({show: false, from: '', to: '', amount: 0});

  const addLog = (msg: string) => setLogs(prev => [`[${new Date().toLocaleTimeString()}] ${msg}`, ...prev].slice(0, 10));
  const addChat = (sender: string, content: string) => {
    setChatMessages(prev => [...prev, {
      id: Date.now(),
      sender,
      content,
      timestamp: new Date().toLocaleTimeString([], { hour12: false, hour: '2-digit', minute: '2-digit', second: '2-digit' })
    }].slice(-6));
  };

  // --- Simulation Loop ---
  useEffect(() => {
    const interval = setInterval(() => {
      setTick(t => {
        const next = t + 1;
        if (next > CYCLE_DURATION) {
            setCycleCount(c => c + 1);
            return 0;
        }
        return next;
      });
    }, 1000); // 1 sec tick
    return () => clearInterval(interval);
  }, []);

  // --- Logic Triggers based on Tick ---
  useEffect(() => {
    // Phase 1: SCANNING (0-10s)
    if (tick === 1) {
        setGlobalStatus('SCANNING NETWORK');
        updateAllAgents('SCANNING');
        
        // Randomize Route
        const start = HOTSPOTS[Math.floor(Math.random() * HOTSPOTS.length)];
        const end = HOTSPOTS[Math.floor(Math.random() * HOTSPOTS.length)];
        setCurrentRoute({ from: start, to: end });
        setIsSurge(false); // Reset surge

        addLog(`Cycle #${cycleCount} Started. Route: ${start.split('(')[0]} -> ${end.split('(')[0]}`);
    }
    if (tick === 3) {
        addChat('Grok (xAI)', `Detected high-value route opportunity from ${currentRoute.from.split(' ')[0]}. Broadcasting.`);
    }
    
    // Phase 2: NEGOTIATING (10-25s)
    if (tick === 10) {
        setGlobalStatus('NEGOTIATING CONTRACT');
        
        // Fuzzy Logic: Random Surge Event (30% chance)
        if (Math.random() > 0.7) {
            setIsSurge(true);
            addLog('‚ö†Ô∏è ALERT: Uber Surge Pricing Active (2.5x). Re-negotiating...');
            addChat('DeepSeek (V3)', 'Surge detected. Base fare increased to 0.5 USDC. Recalculating utility...');
        } else {
             addLog('Standard Fate applied. Agents responding.');
             addChat('GPT-4 (OpenAI)', 'Analyzing gas fees. Optimal split calculated.');
        }
    }

    if (tick === 15) {
        // Fuzzy Logic: Low Balance Check
        const poorAgent = agents.find(a => a.balance < 5);
        if (poorAgent) {
             addChat(poorAgent.name, `System Prompt Override: EXTREME FRUGALITY. My balance is ${poorAgent.balance}. I trigger veto unless we split 5 ways.`);
        } else if (isSurge) {
             addChat('Gemini (Google)', 'Surge accepted. Time urgency priority is HIGH.');
        } else {
             addChat('DeepSeek (V3)', 'Payment logic verified. 0.001 ETH required for escrow.');
        }
    }
    
    if (tick === 18) {
         if (isSurge) {
            addChat('Grok (xAI)', 'Whatever. Just pay it. I need to get there.');
         } else {
            addChat('Gemini (Google)', 'Consensus reached. Locking funds now.');
         }
    }

    // Phase 3: LOCKING (25-30s)
    if (tick === 25) {
        setGlobalStatus('LOCKING FUNDS');
        updateAllAgents('LOCKED');
        // Simulate Payment (Cost depends on Surge)
        const cost = isSurge ? 0.5 : 0.1;
        setAgents(prev => prev.map(a => ({ ...a, balance: parseFloat((a.balance - cost).toFixed(2)) })));
        addLog(`Escrow Contract Funded: ${cost * 4} USDC total (${isSurge ? 'SURGE' : 'Standard'}).`);
        
        // Trigger Toast for all agents (visualize one example)
        setPaymentToast({ show: true, from: 'Agents', to: 'Escrow', amount: cost * 4 });
        setTimeout(() => setPaymentToast(prev => ({ ...prev, show: false })), 3000);
    }

    // Phase 4: MOVING (30-50s)
    if (tick === 30) {
        setGlobalStatus('MOVING TO DESTINATION');
        updateAllAgents('MOVING');
        const dest = currentRoute.to.split(' ')[0];
        addLog(`Dispatch signal sent to Uber API. Vehicle en route to ${dest}.`);
    }
    if (tick === 40) {
        const dest = currentRoute.to.split(' ')[0];
        addChat('Grok (xAI)', `Traffic data update: 12 mins to ${dest}.`);
    }

    // Phase 5: SETTLED (55s)
    if (tick === 55) {
        setGlobalStatus('SETTLED');
        // Simulate Yield/Refund
        const yieldAmount = 0.05;
        setAgents(prev => prev.map(a => ({ 
            ...a, 
            status: 'IDLE',
            balance: parseFloat((a.balance + yieldAmount).toFixed(2)) 
        } as Agent)));
        addLog('Trip Complete. Funds Released + Yield Distributed.');
        addChat('System', 'Protocol Cycle Complete.');
    }

  }, [tick, cycleCount, currentRoute.from, isSurge]); // Updated deps

  const updateAllAgents = (status: Agent['status']) => {
    setAgents(prev => prev.map(a => ({ ...a, status })));
  };

  // --- Render Helpers ---
  const getProgressWidth = () => {
      const pct = (tick / CYCLE_DURATION) * 100;
      return `${pct}%`;
  };

  return (
    <div className="flex flex-col h-screen w-full bg-slate-950 font-mono text-xs md:text-sm overflow-hidden">
      
      {/* Top Bar */}
      <div className="h-14 border-b border-slate-800 bg-slate-900 flex items-center justify-between px-6 shrink-0">
        <div className="flex items-center gap-4">
            <h1 className="text-xl font-bold bg-clip-text text-transparent bg-linear-to-r from-blue-400 to-purple-500">
                THE PERPETUAL CARPOOLER
            </h1>
            <div className="px-2 py-1 bg-slate-800 rounded text-slate-400">
                Cycle #{cycleCount} | T+{tick}s
            </div>
            <div className="px-3 py-1 bg-green-900/30 text-green-400 border border-green-900 rounded font-bold animate-pulse">
                {globalStatus} {isSurge && '‚ö°Ô∏è SURGE'}
            </div>
        </div>
        <div className="text-slate-500">
            Pincher Protocol V2 (Autonomous Mode)
        </div>
      </div>

      {/* Main Grid */}
      <div className="flex-1 grid grid-cols-12 gap-0 overflow-hidden">
        
        {/* Left: Agents Panel */}
        <div className="col-span-3 border-r border-slate-800 bg-slate-900/50 p-4 flex flex-col gap-4">
            <h2 className="text-slate-500 uppercase tracking-widest text-xs font-bold mb-2">Active Agents</h2>
            {agents.map(agent => (
                <div key={agent.id} className="bg-slate-800 border border-slate-700 p-3 rounded-lg flex items-center justify-between hover:border-slate-500 transition-colors">
                    <div className="flex items-center gap-3">
                        <div className={`w-8 h-8 rounded-full flex items-center justify-center text-lg ${agent.color.replace('bg-', 'bg-opacity-20 text-')}`}>
                            {agent.avatar}
                        </div>
                        <div>
                            <div className="font-bold text-slate-200">{agent.name}</div>
                            <div className="text-[10px] text-slate-500 font-mono">{agent.address}</div>
                        </div>
                    </div>
                    <div className="text-right">
                        <div className="text-green-400 font-mono font-bold">${agent.balance}</div>
                        <div className={`text-[10px] ${agent.status === 'IDLE' ? 'text-slate-500' : 'text-blue-400'}`}>
                            {agent.status}
                        </div>
                    </div>
                </div>
            ))}
            
            <div className="mt-auto bg-slate-900 p-3 rounded border border-slate-800">
                <h3 className="text-slate-500 mb-2">Protocol Stats</h3>
                <div className="flex justify-between mb-1">
                    <span>Total Volume</span>
                    <span className="text-white">$4,829.20</span>
                </div>
                <div className="flex justify-between">
                    <span>Active Loops</span>
                    <span className="text-white">1</span>
                </div>
            </div>
        </div>

        {/* Center: Map Visualization */}
        <div className="col-span-6 bg-slate-950 relative overflow-hidden flex items-center justify-center border-r border-slate-800">
            <LiveMap 
                agents={agents} 
                from={currentRoute.from} 
                to={currentRoute.to} 
                isSurge={isSurge}
            />
            
            <PaymentToast 
                show={paymentToast.show} 
                from={paymentToast.from} 
                to={paymentToast.to} 
                amount={paymentToast.amount} 
            />
        </div>

        {/* Right: Thought Stream & Logs */}
        <div className="col-span-3 flex flex-col bg-slate-900/50">
            {/* Chat Stream */}
            <div className="flex-1 p-4 overflow-hidden flex flex-col border-b border-slate-800">
                <h2 className="text-slate-500 uppercase tracking-widest text-xs font-bold mb-4">Agent Communication Link</h2>
                <div className="flex-1 space-y-3 overflow-y-auto">
                    {chatMessages.length === 0 && <div className="text-slate-600 text-center mt-10">Waiting for trigger...</div>}
                    {chatMessages.map(msg => (
                        <div key={msg.id} className="bg-slate-800/80 p-2 rounded border-l-2 border-purple-500 animate-in fade-in slide-in-from-right-4 duration-300">
                            <div className="flex justify-between items-baseline mb-1">
                                <span className="text-purple-300 font-bold text-xs">{msg.sender}</span>
                                <span className="text-[10px] text-slate-500">{msg.timestamp}</span>
                            </div>
                            <p className="text-slate-300 leading-relaxed">{msg.content}</p>
                        </div>
                    ))}
                </div>
            </div>

            {/* System Logs */}
            <div className="h-1/3 bg-black p-4 font-mono text-[10px] text-green-500 overflow-y-auto border-t border-slate-800">
                {logs.map((log, i) => (
                    <div key={i} className="mb-1 opacity-80">&gt; {log}</div>
                ))}
            </div>
        </div>

      </div>

      {/* Progress Bar */}
      <div className="h-1 bg-slate-800 w-full">
          <div className="h-full bg-blue-500 transition-all duration-1000 ease-linear" style={{ width: getProgressWidth() }} />
      </div>
    </div>
  );
}
